button {
  border: none;
  font-family: inherit;
}
.mybtn {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  width: 40px;
  height: 40px;
  border-radius: 15px;
  background-color: #e2e8f5;
  background-image: url("../../../img/1923369.svg");
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
  cursor: pointer;
}
